.profile {
    text-align: center;
    border-radius: 40px;
    background-color: #50bbce;
    padding: 10px;
    margin: 10px;
    box-shadow: 0.4em 0.4em 10px rgba(19, 17, 17, 0.5);
    overflow-y: auto;
    overflow-x: hidden;
}

.profile img{
    border-radius: 1000px;
    width: 250px;
    height: 250px;
    box-shadow: 0.4em 0.4em 10px rgba(19, 17, 17, 0.5);
}

.info{
    border-radius: 40px;
    background-color: #F73859;
    padding: 10px;
    margin: 10px;
    box-shadow: 0.4em 0.4em 10px rgba(19, 17, 17, 0.5);
}

.icon{
    margin-left: 10px;
    height: 40px;
}
.icon img {
    height: 40px;
    width: 40px;
}
